@media screen and (max-width: 1200px){

}

@media screen and (max-width: 992px){
    body{
        font-size: 0.9em;
    }

    .page{
        margin: 0 6em;
    }

    #skill .card, #education .singleCard{
        width: 20em;
    }

    #works ul{
        width: 100%;
        margin: auto;

        flex-wrap: wrap;
    }

    #works .workCard{
        width: 40%;
        height: auto;
        margin: 2em;
    }

    #notes ul{
        width: 100%;
    }
}

@media screen and (max-width: 768px){
    body{
        font-size: 0.8em;
    }

    #coverIcon{
        right: 2em;
    }

    #bgDecoration{
        left: 2em;
    }

    .contactDetail{
        width: 100%;
    }

    .contactDetail a{
        margin: auto;
    }

    #navBtn{
        display: none;
    }

    #navContent{
        transform: translate(0);
    }

    .page{
        margin: 0 4em;
    }

    #bio, #skill, #education{
        flex-direction: column;
    }

    #bio img{
        margin: 1em;
    }

    #bio p{
        margin: 1em;
    }

    #skill .card, #education .singleCard{
        margin-bottom: 2em;
    }

    #works ul{
        flex-direction: column;
        align-items: center;
    }

    .noteBack{
        left: 2em;
        top: 4.2em;
    }

    .workCard .details{
        font-size: 0.8em;
    }

    .workCard .details .workDescription{
        height: 40%;
        overflow-y: scroll;
    }

    .workCard .workLinks{
        margin: 0;
    }

    .workCard .workLinks a{
        margin: 0.4em;
    }
}